<template>
    <yd-layout :class="'groundColor'">
        <div class="mainContentFluid" v-if="agencyFlag">
            <div class="approval">
                选择事务助理
            </div>
            <div class="fluidWidth">
                <div class="tripAppro">
                    <div class="name_circle">
                        <ul>
                            <li v-for="(item, index) in secretariesList">
                                <div class="lastname" v-bind:class="{'check':secretariesNum == index}" v-on:click="secretariesNum=index">
                                    {{item.empname.substring(0,1)}}
                                    <div class="nameClick">
                                    </div>
                                    <div class="imgClick">
                                    </div>
                                </div>
                                <div class="name" v-text="item.empname">
                                </div>
                            </li>
                        </ul>
                        <div class="clear"></div>
                    </div>
                </div>
            </div>
        </div>
        <div class="assistBottom">
            <div class="examineLogo btnOne">
                <yd-flexbox>
                    <div class="examineLogo_txt toBack" @click="$router.go(-1)">返回</div>
                </yd-flexbox>
            </div>
            <div class="examineLogo btnTwo" v-on:click="send()">
                <yd-flexbox>
                    <div class="examineLogo_txt">发送</div>
                </yd-flexbox>
            </div>
        </div>
        <!-- <yd-tabbar slot="tabbar">
            <div class="assistBottom">
                <div class="examineLogo btnOne">
                    <yd-flexbox>
                        <div class="examineLogo_txt toBack" @click="$router.go(-1)">返回</div>
                    </yd-flexbox>
                </div>
                <div class="examineLogo btnTwo" v-on:click="send()">
                    <yd-flexbox>
                        <div class="examineLogo_txt">发送</div>
                    </yd-flexbox>
                </div>
            </div>
        </yd-tabbar> -->
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
export default {
    name: 'lastSend',
    data() {
        return {
            secretariesId: '',
            secretariesList: {},
            secretariesNum: -1,
            addFlag: false,
            sendFlag: false,
            formFlag: false,
            agencyFlag: true,
            formJson: {},
            secondFlag: this.$route.params.secondFlag === 'true' || this.$route.params.secondFlag === true
        }
    },
    mounted() {
        let vm = this;
        document.setTitle('出差审批');
        vm.secretariesList = vm.getTravelInfo.secretariesList;
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getTravelInfo'])
    },
    methods: {
        ...mapMutations([
            'setTravelInfo'
        ]),
        // 送事务助理
        send() {
            let vm = this;
            if (vm.secretariesNum == -1) {
                vm.$dialog.toast({
                    mes: '请选择秘书',
                    timeout: 2000
                });
            } else {
                vm.secretariesId = vm.secretariesList[vm.secretariesNum].operatorid;
                vm.get(vm.conf.portalMobile + '/API/WorkFlow/oawf/doOANext', {
                    'type': 'travelremark',
                    'workItemID': vm.$route.params.workId,
                    'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                    'activityID': 'travelSecrataryActivity',
                    'leaderIDs': vm.secretariesId,
                    'fuyan': '',
                    'opinion': '',
                    'workTitle': vm.getTravelInfo.firstorgname + vm.getTravelInfo.confirmItem + '出差备案申请',
                    'ext': '{"applicantid":"' + vm.getTravelInfo.applicantid + '","applicantname":"' + vm.getTravelInfo.confirmItem + '","startdate":"' + vm.getTravelInfo.startdate + '","enddate":"' + vm.getTravelInfo.enddate + '","traveldays":"' + vm.getTravelInfo.traveldays + '","travelplace":"' + vm.getTravelInfo.travelplace + '","othertravelnames":"' + vm.getTravelInfo.othertravelnames + '","othertravelids":"' + vm.getTravelInfo.othertravelids + '","reason":"' + vm.getTravelInfo.reason + '","reason2":"' + vm.getTravelInfo.reason2 + '"}'
                }, function(res) {
                    if (res.isSuccess == 'true') {
                        vm.setTravelInfo({
                            formJson: {},
                            approver: [],
                            secretariesList: [],
                            applyList: [],
                            reason: '',
                            confirmItem: '',
                            startdate: '',
                            enddate: '',
                            applicantid: '',
                            traveldays: '',
                            travelplace: '',
                            othertravelnames: '',
                            selectothertravelnames: {},
                            reason2: '',
                            firstorgname: '',
                            applicantUserId: '',
                            statusRadio: 0,
                            jsonData: {}
                        })
                    }
                    vm.$dialog.toast({
                        mes: '发送成功',
                        timeout: 2000,
                        callback: () => {
                            vm.$router.push('/handle');
                        }
                    });
                });
            }
        }
    }
}
</script>
<style scoped>
.check .nameClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 2;
    opacity: 0.5;
    background-color: #256f7b;
    position: absolute;
    top: 0;
    right: 0;
}

.check .imgClick {
    width: 0.81rem;
    height: 0.81rem;
    margin: 0 auto;
    border-radius: 1rem;
    text-align: center;
    z-index: 99;
    position: absolute;
    top: 11px;
    right: -6px;
    background-image: url(../../../static/images/dh.png);
    background-repeat: no-repeat;
    background-size: 0.6rem;
}

.name_circle {
    margin-top: 0.5rem;
}

.approval {
    font-size: 0.32rem;
}

.mainContentFluid {
    margin-top: 0;
    /*height: 100%;*/
}

.groundColor {
    background-color: #fff;
}

.approval {
    font-size: 0.32rem;
    padding-left: 0.53rem;
    padding-top: 0.27rem;
    font-weight: normal;
    border-bottom: 1px dashed #eaeaea;
}

.assistBottom {
    border-top: none;
    position: relative;
    padding: 0 .2rem;
    background-color: #fff;
    margin-top: 1rem;
}

.examineLogo {
    height: 0.99rem;
    margin-top: 0;
    border-right: none;
    border-radius: 5px;
    width: 48%;
}

.examineLogo .yd-flexbox {
    line-height: 0.99rem;
}

.examineLogo_txt {
    color: #fff;
    font-size: 0.36rem;
    font-weight: normal;
}

.btnOne {
    background: #fafafa;
    letter-spacing: 0.1rem;
    border: 1px solid #eaeaea;
}

.btnTwo {
    background-color: #46a4f0;
    float: right;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.toBack {
    color: #333;
}
</style>


